body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* CSS f端r Header*/

*{
  margin:0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.header{
  width: 100%;
  height: 80px;
  display: block;
  background-color: black;
}

.inner_header{
  width: 1000px;
  height: 100%;
  display: block;
  margin: 0 auto;
}

.logo_container{
  height: 100%;
  display: table;
  float: left;
}

.logo_part{
  padding: 5px;
}

.logo_container h1{
  color:white;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  font-family: 'Open Sans', sans-serif;
  font-size: 32px;
  font-weight: 200;
}

.logo_container h1 span{
  font-weight: 800;
}

.navigation{
 float: right;
 height: 100%;

}

.navigation li{
  height: 100%;
  display: table;
  float: left;
  padding: 0px 30px;
}

.navigation li:last-child{
  padding-right: 0px;
}

.navigation li a{
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}

/* CSS f端r Footer*/

*{
	margin:60;
	padding:60;
	list-style:none;
	text-decoration:none;
}

.footer{
	width:100%;
	display:block;
	padding:20px;
	box-sizing:border-box;
	background-color: black;
}

.inner_footer{
    width: 1200px;
    height: 100%;
    display: block;
    margin: 0 auto;
}

.inner_footer .footer_third{
  padding: 50px;
	width:calc(21.666666667% -20px);
	margin-right:10px;
	float:left;
	height:100%;
}

.inner_footer .footer_third:last-child{
	margin-right:0;
	
}

.inner_footer .footer_third h1{
	font-family: 'Open Sans', sans-serif;
	font-size:22px;
	color:white;
	display:block;
	width:100%;
	margin-bottom:20px;
}

.inner_footer .footer_third a{
	font-family: 'Open Sans', sans-serif;
	font-size:18px;
	color:white;
	display:block;
	font-weight:200;
	width:100%;
	padding-bottom:5px;
}

.inner_footer .footer_third li{
	display:inline-block;
	padding:0 5px;
	font-size:20px;
	
}

.inner_footer .footer_third span{
		color:white;
		font-family:'Open Sans', sans-serif;
		font-size:16px;
		font-weight:200;
		display:block;
		width:100%;
		padding-top:20px;
}

.copyright{
  margin-left:400px;
  padding: 10px;
  color: white;
  font-family:'Open Sans', sans-serif;
  font-size:14px;
}



/* CSS f端r Fehlerseite*/

.error h1{
  font-family: 'Open Sans', sans-serif;
  font-size: 30px;
  font-weight: 600;
  color: red;
}

.error h4{
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  font-weight: 700;
  color: black;
}


/* CSS f端r Startseite*/

